<template>
  <div id="app">
    <div class="container">
      <div class="row">
        <div class="col-12 mt-2">
            <div><h3>
                Welcome to procurement system, this is your first login<br/>
                You are required to create signature which will be stored for further uses.
                </h3>
                <br/><br/>
            </div>
          <div style='display:flex;flex-direction:row'>
            <div class="col-6 mt-2">
            <VueSignaturePad
              id="signature"
              width="100%"
              height="200px"
              ref="signaturePad"
            />
            <div style='margin-top:0.5em;margin-left:20%;margin-right:20%'>
              <button
                class="btn btn-outline-secondary float-right"
                @click="undo"
              >
                Undo
              </button>
              <button
                class="btn btn-outline-primary float-left"
                @click="save"
              >
                Save
              </button>
            </div>
          </div>
          <div style='display:flex;flex-direction:column'>
            <div style='margin-top:0.5em'>
                <label>Create Signature Security Code (min. 6 characters) :</label>
                <input type="password" class='form-control input' v-model='pwd'/>
            </div>
            <div style='margin-top:3.5em'>
                <label>Security Code Verification :</label>
                <input type="password" class='form-control input' v-model='pwd_check'/>
            </div> 
          </div>       
        </div>        
        </div>


      </div>
    </div>
  </div>
</template>

<script>
export default {
    data(){
        return{
            pwd:'',
            pwd_check:'',
            return:'',
        }
    },
  name: 'user_setup',
  methods: {
    undo() {
      this.$refs.signaturePad.undoSignature();
      this.pwd='';
      this.pwd_check='';
    },
    save() {
      const { isEmpty, data } = this.$refs.signaturePad.saveSignature();
      if(isEmpty){
          this.$alert('Signature board cannot be empty!');
      } else if(this.pwd.length==0){
            this.$alert('Security Code Cannot be empty!');
      } else if(this.pwd.length<=5){
            this.$alert('Min. Length of Security Code is 6');
      } else if(this.pwd!=this.pwd_check){
            this.$alert('Security Code Validation Failure! Please check your inputs');
      } else {
          var sign = data;
          var formData = new FormData();
          formData.append('sign',data);
          formData.append('pwd',this.pwd);
          axios.post('Procurement/shared_backend/user_setup.php',formData);
          this.$alert('Successfully completed setup, return to previous page')
          this.$router.push('../RequestPortal/PortalHome');
        }     
    }
  }
};
</script>

<style>
#signature {
  border: double 3px transparent;
  border-radius: 5px;
  background-image: linear-gradient(white, white),
    radial-gradient(circle at top left, #4bc5e8, #9f6274);
  background-origin: border-box;
  background-clip: content-box, border-box;
}
</style>



